import styled from '@emotion/styled';
import { hoverTextUnderline, mobile } from 'components/Common/GlobalStyle';
import React from 'react';
import ProjectCard from './ProjectCard';
import ProjectCarousel from './ProjectCarousel';
import test from 'images/projects/test.png';

const ProjectCards = () => {
  return (
    <div>
      <ProjectCard
        title=" N"
        period="2022.02 (1  )"
      >
        <ProjectCarousel imgs={[test, test, test, test, test]} />
        <div>
          <ProjectsDescriptionsMain>
            <b>
                    
                 
            </b>
            .          
                   
            .
            <P />
             Django     , React 
              React Django   .  
            Django REST Framework       ,
            AWS   , ,     
            .
          </ProjectsDescriptionsMain>
          <ProjectsDescription>
            <ProjectLabel> </ProjectLabel>
            <ProjectValue></ProjectValue>
          </ProjectsDescription>
          <ProjectsDescription>
            <ProjectLabel>GitHub</ProjectLabel>
            <ProjectValue>
              <ProjectsUrl>https://github.com/dnr14</ProjectsUrl>
            </ProjectValue>
          </ProjectsDescription>
          <ProjectsDescription>
            <ProjectLabel>URL</ProjectLabel>
            <ProjectValue>
              <ProjectsUrl>https://github.com/dnr14</ProjectsUrl>
            </ProjectValue>
          </ProjectsDescription>
          <ProjectsDescription>
            <ProjectLabel></ProjectLabel>
            <ProjectValue></ProjectValue>
          </ProjectsDescription>
          <ProjectsDescription>
            <ProjectLabel></ProjectLabel>
            <ProjectValue></ProjectValue>
          </ProjectsDescription>
          <ProjectsDescription>
            <ProjectLabel></ProjectLabel>
            <ProjectValue></ProjectValue>
          </ProjectsDescription>
        </div>
      </ProjectCard>
      {/* <ProjectCard />
      <ProjectCard />
      <ProjectCard />
      <ProjectCard />
      <ProjectCard /> */}
    </div>
  );
};

const ProjectsDescriptionsMain = styled.div`
  padding-bottom: 1rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid #ccc;
  font-weight: 400;
  font-size: 1rem;
`;

const ProjectsDescription = styled.div`
  margin-bottom: 1rem;
`;

const ProjectLabel = styled.div`
  display: inline-block;
  vertical-align: top;
  width: 8.4rem;
  font-weight: 900;
  font-size: 1rem;
  ::before {
    content: '';
    display: inline;
    padding-right: 0.5rem;
    ${mobile} {
      display: none;
    }
  }

  ${mobile} {
    display: table;
    width: auto;
    padding-left: 0.5rem;
    margin-bottom: 0.25rem;
    border-left: 5px solid #222;
  }
`;

const ProjectValue = styled.div`
  display: inline-block;
  vertical-align: bottom;
  width: calc(100% - 8.4rem);
  font-weight: 400;
  font-size: 1rem;
  ${mobile} {
    width: 100%;
  }
`;

const ProjectsUrl = styled.a`
  display: block;
  color: #258ddb;
  cursor: pointer;
  word-break: break-all;
  ${hoverTextUnderline}
`;

const P = styled.p`
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
`;

export default ProjectCards;
